@import '../../App.scss';

@import '../../App.scss';

.home {
    margin: 8rem 0 0 3rem;
    padding-right: 0.5rem;
    display: flex;
    flex-direction: column;

    h2 {
        font-size: 2rem;
        font-weight: 600;
        color: $color-light;
    }

    p {
        margin: 0.5rem 0;
        font-size: 1rem;
        font-weight: 400;
        color: $color-light;

    }

    span {
        font-size: 1rem;
        font-weight: 800;
        color: $color-primary;
        margin-left: 0.5rem;
    }

    @media screen and (min-width: 768px) {
        margin: 8rem 0 0 7rem;
    }

    @media screen and (min-width: 1024px) {
        margin: 10rem 0 0 9rem;
        flex-direction: row;
    }

    .trending-section {
        padding-right: 2rem;
        width: 100%;

        @media screen and (min-width: 1024px) {
            border-right: 3px solid $color-light;
            width: 25%;
        }

        .trending-title {
            font-size: 2rem;
            font-weight: 600;
            color: $color-primary;
            margin: 0 0 2rem 0;

        }

        .trending-list {
            margin: 1rem 0;
            width: 100%;

            ul {
                display: flex;
                flex-direction: column;
                align-items: start;
                width: 100%;
                gap: 1rem;

                li {
                    flex: 1;
                    list-style: none;
                    text-align: left;
                    text-transform: capitalize;
                    font-size: 1rem;
                    font-weight: 600;
                    color: $color-secondary;
                    cursor: pointer;

                    width: 100%;

                    &:hover,
                    &:active,
                    &:focus {
                        color: $color-primary;
                    }

                    img {
                        width: 18rem;
                        height: 5rem;
                        object-fit: cover;

                        @media screen and (min-width: 768px) {
                            width: 100%;
                            height: 8rem;
                        }

                        @media screen and (min-width: 1024px) {
                            width: 100%;
                            height: 12rem;
                        }
                    }
                }
            }
        }
    }

    .search-section {
        flex: 1;
        padding-right: 2rem;
        margin: 2rem 1rem 0 1rem;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        width: 100%;
    }
}
