@import '../../App.scss';

.search-form-books {
    display: flex;
    gap: 1rem;
    border-bottom: 1px solid $color-light;
    width: 80%;

    @media screen and (min-width: 768px) {
        width: calc(100% - 5rem);
    }

    @media screen and (min-width: 1024px) {
        width: calc(60% - 5rem);
    }

    input {
        box-sizing: border-box;
        width: 100%;
        padding: 10px;
        font-size: 16px;
        font-weight: 400;
        border: none;
        outline: none;
        background-color: transparent;
        color: $color-light;
    }

}

.container {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.5rem;

    p {
        font-size: 1rem;
        font-weight: 400;
        color: $color-primary !important;
        padding: 10px;
    }
}

button {
    border: none;
    outline: none;
    font-size: 1.2rem;
    background-color: transparent;
    padding: 10px;
    display: flex;
    align-items: center;

    ion-icon {
        cursor: pointer;
        color: $color-primary;
    }
}
